.main {
    background-color: #4C4C78;
}

.header {
    padding: 40px 0;
    padding-left: 70px;
    position: absolute;
}

.row {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 95vh;
    padding-left: 70px;
    box-sizing: border-box;
}

.left {
    height: 100%;
    display: flex;
    align-items: center;
}

.right {
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 4vw;
    overflow: hidden;
}

.features {
    background-color: white;
}

.callToAction {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 0 200px;
}

.title {
    font-family: Roboto;
    font-size: 32px;
    font-weight: 600;
    color: #4C4C78;
    padding: 15px 0;
}

.inputContainer {
    width: 600px;
    text-align: center;
    padding-top: 30px;
}

.inputContainer > div > input {
    background-color: rgb(240, 240, 240);
}

.dashboard {
    text-align: center;
    padding: 50px 0;
    background-color: rgb(245, 245, 255);
}

.dashboard > img {
    height: 450px;
}

.mainText {
    font-family: Roboto;
    font-size: 32px;
    font-weight: 600;
    color: #4C4C78;
}

.subText {
    font-family: Roboto;
    padding-top: 15px;
    font-size: 16px;
    font-weight: 500;
    color: #333333;
}

@media only screen and (max-width: 1000px) {
    .revenue {
        flex-direction: column;
    }

    .revenueText {
        width: 90%;
        padding-left: 0;
        padding-top: 40px;
        text-align: center;
    }

    .dashboard {
        overflow: hidden;
    }

    .dashboard > img {
        padding-left: 20px;
        height: 400px;
    }
}

@media only screen and (max-width: 650px) {
    .revenueDescription {
        width: auto;
    }

    .right {
        display: none;
    }

    .header {
        width: 100%;
        padding-left: 0;
        padding-top: 35px;
        display: flex;
        justify-content: center;
    }

    .row {
        width: 100%;
        padding: 0 15px;
        display: inline-block;
        text-align: center;
    }

    .inputContainer {
        width: 90%;
    }
}