.root {
    position: fixed;
    z-index: 5;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal {
    width: 400px;
    height: 500px;
    background-color: white;
    border-radius: 15px;
    text-align: center;
    padding: 25px 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.header {
    font-family: Roboto;
    font-weight: 600;
    font-size: 22px;
    color: #212121;
}

.subHeader {
    margin-top: 10px;
    font-family: Roboto;
    font-weight: 400;
    font-size: 18px;
    color: #3a3a3a;
}

.share {
    margin-top: 20px;
    font-family: Roboto;
    font-weight: 400;
    font-size: 14px;
    color: #3a3a3a;
}

.url {
    width: 100%;
    margin-top: 20px;
    font-family: Roboto;
    font-weight: 400;
    font-size: 14px;
    color: white;
    background-color: #202134;
    padding: 12px 0;
    border-radius: 5px;
}

@media only screen and (max-width: 650px) {
    .modal {
        width: 80%;
    }
}