.root {
    background-color: white;
}

.body {
    margin: auto;
    max-width: 600px;
    padding: 60px 0;
}

.headerLogo {
    text-align: center;
}

.headerLogoText {
    font-family: Inversionz;
    font-size: 60px;
    color: #6C64FE;
    cursor: pointer;
}

.header {
    font-family: Roboto;
    font-weight: 600;
    font-size: 24px;
    color: #6C64FE;
    padding: 15px 0;
}

.text {
    font-family: Roboto;
    font-weight: 400;
    font-size: 14px;
    color: #333333;
    padding: 5px 0;
}

.subtext {
    font-family: Roboto;
    font-weight: 400;
    font-size: 12px;
    color: #777777;
    padding: 0 0 5px 8px;
    margin-top: -2px;
}

.bold {
    font-weight: 600;
}

.logoRow {
    display: flex;
    justify-content: space-evenly;
    padding: 40px;
}

.logo {
    width: auto;
    height: 60px;
}

.logoWhite {
    width: auto;
    height: 60px;
}

.headshotRow {
    display: flex;
    align-items: center;
    padding: 60px 0 0;
}

.headshot {
    width: auto;
    height: 120px;
}

.headshotText {
    padding-left: 20px;
}

.subtext2 {
    font-family: Roboto;
    font-weight: 600;
    font-size: 16px;
    color: #333333;
    margin-top: -6px;
}

@media only screen and (max-width: 700px) {
    .root {
        margin: 0 15px;
    }

    .logoRow {
        padding: 30px;
    }
}