.root {
    font-family: 'Inversionz';
    color: white;
    font-size: 50px;
    width: 500px;
}

.main {
    font-family: Roboto;
    color: white;
    font-size: 36px;
    font-weight: 600;
}

.sub {
    padding: 25px 0;
    font-family: Roboto;
    color: white;
    font-size: 20px;
    font-weight: 500;
}

@media only screen and (max-width: 650px) {
    .main {
        font-size: 36px;
    }

    .sub {
        padding-top: 20px;
        padding-bottom: 25px;
        font-size: 18px;
    }
}