a {
    font-family: Roboto;
    text-decoration: none;
}

.root {
    background-color: #4C4C78;
    height: 200px;
    padding: 0 70px;
    position: relative;
}

.logoContainer {
    float: left;
    vertical-align: middle;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.buttons {
    float: right;
    vertical-align: middle;
    margin-top: 100px;
    transform: translateY(-50%);
}

.social {
    padding: 0 8px;
    height: 15px;
    vertical-align: middle;
}

.button {
    font-family: Roboto;
    color: white;
    font-weight: 500;
    font-size: 16px;
}

@media only screen and (max-width: 700px) {
    .root {
        padding: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .logoContainer {
        position: initial;
        float: none;
        margin-top: 0;
        transform: 0;
    }

    .buttons {
        float: none;
        margin-top: 10px;
        transform: 0;
    }
}