.root {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 120px 0;
}

.imageContainer {
    height: 400px;
}

.image {
    height: 100%;
}

.image2 {
    height: 314px;
    z-index: 10;
    margin-bottom: -314px;
}

.textContainer {
    padding-left: 50px;
}

.title {
    font-size: 32px;
    font-weight: 600;
    color: #4C4C78;
}

.text {
    padding-top: 15px;
    font-size: 16px;
    font-weight: 500;
    color: #333333;
    width: 420px;
}

.spacer {
    padding-bottom: 14px;
}

.right {
    flex-direction: row-reverse;
    text-align: right;
}

.right > .textContainer {
    padding-left: 0;
    padding-right: 50px;
}

@media only screen and (max-width: 1000px) {
    .root {
        flex-direction: column;
        text-align: center;
    }
    .imageContainer {
        height: auto;
        width: 80%;
    }
    .image {
        height: auto;
        width: 100%;
    }
    .textContainer {
        padding: 30px 0 0 0 !important;
    }
    .text {
        width: 80%;
        margin: auto;
    }
}