input, button, submit {
    border: none;
    padding: 0;
    margin: 0;
}

input:focus {
    outline: none;
}

.root {
    width: 100%;
}

.input {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background-color: white;
    color: #333333;
    font-family: Roboto;
    font-weight: 400;
    font-size: 16px;
    padding: 12px 18px;
    width: 70%;
    box-sizing: border-box;
}

.input::placeholder {
    color: #BDBDBD;
}

.button {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: #FFE55B;
    color: #333333;
    font-family: Roboto;
    font-weight: 500;
    font-size: 16px;
    padding: 12px 0;
    width: 30%;
    cursor: pointer;
}

.button:hover {
    background-color: rgb(236, 212, 88);
}

.error {
    font-family: Roboto;
    font-weight: 500;
    color: #FF6363;
    font-size: 18px;
    float: right;
    padding-top: 10px;
}

@media only screen and (max-width: 650px) {
    .input {
        display: block;
        width: 100%;
        padding: 10px;
        border-radius: 10px;
    }

    .button {
        margin-top: 15px;
        border-radius: 10px;
        width: 60%;
        padding: 10px;
    }

    .error {
        display: block;
        float: none;
        padding-top: 5px;
    }
}